                content: '<div class="article" id="artibody" data-sudaclick="blk_content"> <div class="img_wrapper"> <!--//n.sinaimg.cn/sports/transform/270/w650h420/20190507/20d3-hwsffza6790553.jpg--> <img alt="川崎主帅鬼木达" src="//n.sinaimg.cn/sports/transform/270/w650h420/20190507/20d3-hwsffza6790553.jpg"> <span class="img_descr">川崎主帅鬼木达</span> </div><p>　　北京时间5月7日晚18点，2019年亚冠联赛小组赛H组第五轮赛事在川崎等等力陆上竞技场开始一场较量，由川崎前锋主场迎战上海上港。上半场第6分钟，胡尔克从左侧带球奔袭大半场，过掉川崎门将郑成龙之后推射空门得分。第12分钟达米昂和斋藤学配合之后，达米昂灵活控球，避开上港队员防守后中路射门得分。第39分钟胡尔克在右路传中，张卫头球打中横梁弹出。上半场两队1比1踢平。第65分钟守田英正左路传中，谷口彰悟头球得分。第70分钟奥斯卡左路传中，胡尔克跃起顶反弹球扳平比分。最终两队2比2踢平，上港5战6分继续排名小组第二，川崎5战5分小组第三。</p> <p>　　比赛结束之后，川崎前锋主帅鬼木达点评了本场比赛，“我们经历很多支持者到场的一场比赛，但是我们没有赢得比赛，对此我很失望，也非常抱歉，我们知道今天这是一场必须要取胜的比赛，但是我们在丢的第一个球的时候，丢球方式是令我失望的，我们应该阻止他们完成那个进球，这方面回去总结加强。”</p> <p>　　对于今天在比赛中传球方面出现的一些失误，鬼木达说：“在我看来，上半场两队打得还可以，丢第二个球之前场上表现都是可控的，但是，亚冠赛场上，一个球的领先是不够的，所以当时我们希望打进第三个进球，在那个时间段我们的传球有失误，对于这一点我确实有些失望，我们的失误也最终导致了对手追平了比分。”</p> <p>　　与去年相比看到了一些进步，但是我们上一场没有拿下澳大利亚球队，今天没有拿下上港，球迷们可能还是有些失望的，对于小组赛最后一场的走势，主教练如何看待？对此，鬼木达坦言：“现在的情况对于我们来说，确实挺困难的，因为上港是主场比赛，我们现在只有一条路，就是必须赢得比赛，我非常明白球迷的期望，我们会为他们而战，希望球队更加成熟，不管心理上还是身体上。”（曲小尤）</p> <!-- 非定向300*250按钮 17/09 wenjing begin --> <div id="left_hzh_ad"> <!-- <script async charset="utf-8" src="//d5.sina.com.cn/litong/zhitou/sinaads/release/sinaads.js"></script> <script language="javascript" type="text/javascript" src="//d2.sina.com.cn/d1images/button/rotator.js"></script> <script type="text/javascript"> (function(){ var adScript = document.createElement("script"); adScript.src = "//d1.sina.com.cn/litong/zhitou/sinaads/demo/changwy/link/yl_left_hzh_20160119.js"; document.getElementsByTagName("head")[0].appendChild(adScript); })(); </script> --> </div> <!-- 非定向300*250按钮 end --> </div>',
                ptime: null
